const filmDataSF = [
    {
        countries: [
            {
                country: "США",
            },
    
        ]
    }
] ;


const tableBodySF = document.getElementById("sf-table");
tableBodySF.innerHTML = "";

for (let index = 0; index < filmDataSF.length; index++) {
    const filmSF = new FilmSF(filmDataSF[index]);

    if (filmSF.isNotForAdult()) {        
        tableBodySF.innerHTML += filmSF.renderFilmTableItemSF();
    }
}